import React from 'react';
import { useDispatch, useSelector } from 'react-redux';

const PaymentMethod = ({
  paymentMethod,
  setPaymentMethod,
  cart = [], //  Accept cart as prop with default empty array
  totalPrice = 0, //  Accept totalPrice as prop
  onBackToAddress,
  onPlaceOrder,
  loading
}) => {
  const dispatch = useDispatch();
  const deliveryAddress = useSelector((state) => state.deliveryAddress);
  const { error } = useSelector((state) => state.orders);

  return (
    <div className="min-h-screen bg-gray-100 py-5 px-4">
      <div className="max-w-3xl mx-auto">
        <h2 className="text-3xl font-bold mb-6 text-blue-600">
          Confirm Your Order
        </h2>

        {/* Delivery Information */}
        <div className="bg-white rounded-lg p-6 mb-6 shadow-sm">
          <h3 className="mb-4 text-xl font-semibold">
            Delivery Information
          </h3>
          <div className="text-sm leading-relaxed">
            <p className="mb-2"><strong>Name:</strong> {deliveryAddress.fullName}</p>
            <p className="mb-2"><strong>Phone:</strong> {deliveryAddress.phone}</p>
            <p className="mb-2"><strong>Address:</strong> {deliveryAddress.address}</p>
            {deliveryAddress.city && <p className="mb-2"><strong>City:</strong> {deliveryAddress.city}</p>}
            {deliveryAddress.pincode && <p className="mb-2"><strong>Pincode:</strong> {deliveryAddress.pincode}</p>}

            {deliveryAddress.mapLink && (
              <div className="mt-4 p-3 bg-green-100 rounded border border-green-300">
                <p className="m-0 text-green-800">
                   Location saved:{' '}
                  <a
                    href={deliveryAddress.mapLink}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="text-blue-600 underline"
                  >
                    View on Google Maps
                  </a>
                </p>
                <p className="mt-2 mb-0 text-xs text-green-800">
                  Coordinates: {deliveryAddress.latitude?.toFixed(6)}, {deliveryAddress.longitude?.toFixed(6)}
                </p>
              </div>
            )}
          </div>
        </div>

        {/* Order Summary */}
        <div className="bg-white rounded-lg p-6 mb-6 shadow-sm">
          <h3 className="mb-4 text-xl font-semibold">
            Order Summary
          </h3>
          {cart && cart.length > 0 ? (
            <>
              {cart.map((item, index) => (
                <div 
                  key={index} 
                  className={`flex justify-between mb-3 pb-3 ${
                    index < cart.length - 1 ? 'border-b border-gray-200' : ''
                  }`}
                >
                  <div>
                    <p className="m-0 font-medium">{item.pickleName}</p>
                    <p className="m-0 text-sm text-gray-600">
                      Quantity: {item.quantity}  {(item.subtotal / item.quantity).toFixed(2)}
                    </p>
                  </div>
                  <p className="m-0 font-semibold text-green-700">
                    {item.subtotal.toFixed(2)}
                  </p>
                </div>
              ))}
              <hr className="my-4 border-0 border-t-2 border-blue-600" />
              <div className="flex justify-between text-2xl font-bold text-blue-600">
                <span>Total:</span>
                <span>{totalPrice.toFixed(2)}</span>
              </div>
            </>
          ) : (
            <p className="text-gray-600">Your cart is empty</p>
          )}
        </div>

        {/* Payment Method */}
        <div className="bg-yellow-100 p-6 rounded-lg mb-6 border border-yellow-400">
          <h3 className="mb-2 text-lg font-semibold">Payment Method</h3>
          <p className="m-0 text-yellow-800 text-base">
             Cash on Delivery (COD)
          </p>
        </div>

        {/* Error Message */}
        {error && (
          <div className="bg-red-100 text-red-800 p-4 rounded mb-4 border border-red-300">
            <strong>Error:</strong> {error}
          </div>
        )}

        {/* Action Buttons */}
        <div className="flex gap-4">
          <button
            onClick={onBackToAddress}
            disabled={loading}
            className={`flex-1 py-4 bg-white text-blue-600 border-2 border-blue-600 rounded text-base font-semibold ${
              loading ? 'cursor-not-allowed opacity-60' : 'cursor-pointer hover:bg-blue-50'
            }`}
          >
            Back to Address
          </button>
          <button
            onClick={onPlaceOrder}
            disabled={loading || !cart || cart.length === 0}
            className={`flex-1 py-4 text-white border-none rounded text-base font-semibold ${
              loading || !cart || cart.length === 0
                ? 'bg-gray-400 cursor-not-allowed'
                : 'bg-green-700 cursor-pointer hover:bg-green-800'
            }`}
          >
            {loading ? 'Placing Order...' : 'Place Order (COD)'}
          </button>
        </div>
      </div>
    </div>
  );
};

export default PaymentMethod;