import React from 'react';
import {
  Box, Card, CardContent, Button, Typography
} from '@mui/material';
import { CheckCircle } from '@mui/icons-material';

const OrderConfirmation = ({
  lastOrderId,
  onContinueShopping
}) => {
  return (
    <Box sx={{ textAlign: 'center', py: 6 }}>
      <CheckCircle sx={{ fontSize: 100, color: '#2e7d32', mb: 3 }} />
      <Typography variant="h3" sx={{ fontWeight: 700, mb: 2, color: '#2e7d32' }}>
        Order Placed Successfully!
      </Typography>
      <Typography variant="h6" color="textSecondary" sx={{ mb: 4 }}>
        Thank you for your order!
      </Typography>
      <Card sx={{ maxWidth: 500, mx: 'auto', p: 4, mb: 4 }}>
        <Typography variant="body1" sx={{ mb: 2 }}>
          Order ID: <strong>#{lastOrderId}</strong>
        </Typography>
        <Typography variant="body1" sx={{ mb: 2 }}>
          Estimated Delivery: <strong>7 days</strong>
        </Typography>
        <Typography variant="body2" color="textSecondary">
          You can track your order in "My Orders" section
        </Typography>
      </Card>
      <Button
        variant="contained"
        onClick={onContinueShopping}
        size="large"
        sx={{ bgcolor: '#1976d2' }}
      >
        Continue Shopping
      </Button>
    </Box>
  );
};

export default OrderConfirmation;
