import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { ShoppingCart, Menu as MenuIcon, Person, Search } from '@mui/icons-material';
import { logout, clearSessionExpired } from '../store/slices/authSlice';
import { fetchPicklesRequest } from '../store/slices/pickleSlice';
import { fetchOrdersRequest, createOrderRequest } from '../store/slices/orderSlice';
import {
  fetchCartRequest,
  addToCartRequest,
  updateCartItemRequest,
  removeFromCartRequest,
  clearCartRequest
} from '../store/slices/cartSlice';
import { clearDeliveryAddress } from '../store/slices/deliveryAddressSlice';
import BrandLogo from '../assets/BrandLogo.png';
import Cart from '../components/Cart';
import PicklesList from '../components/PicklesList';
import DeliveryAddress from '../components/DeliveryAddress';
import PaymentMethod from '../components/PaymentMethod';
import OrderConfirmation from '../components/OrderConfirmation';
import NavigationDrawers from '../components/NavigationDrawers';
import About from './About';
import ContactPage from './ContactPage';
import Footer from '../components/Footer';

const CustomerDashboard = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [isMobile, setIsMobile] = useState(window.innerWidth < 960);

  const { user, sessionExpired } = useSelector((state) => state.auth);
  const { pickles } = useSelector((state) => state.pickles);
  const { items: cart, totalItems, totalPrice, loading: cartLoading } = useSelector((state) => state.cart);
  const deliveryAddress = useSelector((state) => state.deliveryAddress);

  const [drawerOpen, setDrawerOpen] = useState(false);
  const [cartDrawerOpen, setCartDrawerOpen] = useState(false);
  const [userDrawerOpen, setUserDrawerOpen] = useState(false);
  const [currentStep, setCurrentStep] = useState(0);
  const [loading, setLoading] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('ALL');
  const [currentView, setCurrentView] = useState('shop');
  const [paymentMethod, setPaymentMethod] = useState('COD');
  const [snackbar, setSnackbar] = useState({ open: false, message: '', severity: 'success' });
  const [lastOrderId, setLastOrderId] = useState(null);

  // Handle session expiry
  useEffect(() => {
    if (sessionExpired) {
      setSnackbar({
        open: true,
        message: 'Your session has expired. Please login again.',
        severity: 'error'
      });
      
      // Auto-redirect to login after 3 seconds
      const timer = setTimeout(() => {
        dispatch(clearSessionExpired());
        navigate('/login');
      }, 3000);

      return () => clearTimeout(timer);
    }
  }, [sessionExpired, navigate, dispatch]);

  useEffect(() => {
    const handleResize = () => setIsMobile(window.innerWidth < 960);
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  useEffect(() => {
    dispatch(fetchPicklesRequest());
    dispatch(fetchOrdersRequest());
    if (user) {
      dispatch(fetchCartRequest());
    }
  }, [dispatch, user]);

  const handleLogout = () => {
    dispatch(logout());
    navigate('/login');
  };

  const handleAddToCart = async (pickle) => {
    try {
      dispatch(addToCartRequest({
        pickleId: pickle.id,
        quantity: 1,
        selectedWeight: pickle.selectedWeight || pickle.baseWeight || 250
      }));
      setSnackbar({ open: true, message: `${pickle.name} added to cart!`, severity: 'success' });
    } catch (error) {
      setSnackbar({ open: true, message: error?.message || 'Failed to add to cart', severity: 'error' });
    }
  };

  const handleBuyNow = async (pickle) => {
    try {
      dispatch(clearCartRequest());
      setTimeout(() => {
        dispatch(addToCartRequest({
          pickleId: pickle.id,
          quantity: 1,
          selectedWeight: pickle.selectedWeight || pickle.baseWeight || 250
        }));
        setCurrentStep(1);
        setCurrentView('shop');
        setCartDrawerOpen(false);
      }, 500);
    } catch (error) {
      setSnackbar({ open: true, message: 'Failed to process', severity: 'error' });
    }
  };

  const handleUpdateQuantity = async (cartItemId, newQuantity) => {
    try {
      if (newQuantity > 0) {
        dispatch(updateCartItemRequest({ cartItemId, quantity: newQuantity }));
      } else {
        dispatch(removeFromCartRequest(cartItemId));
      }
    } catch (error) {
      setSnackbar({ open: true, message: 'Failed to update cart', severity: 'error' });
    }
  };

  const handleRemoveFromCart = async (cartItemId) => {
    try {
      dispatch(removeFromCartRequest(cartItemId));
      setSnackbar({ open: true, message: 'Item removed from cart', severity: 'success' });
    } catch (error) {
      setSnackbar({ open: true, message: 'Failed to remove item', severity: 'error' });
    }
  };

  const handleCheckoutFromCart = () => {
    if (!cart || cart.length === 0) {
      setSnackbar({ open: true, message: 'Your cart is empty!', severity: 'warning' });
      return;
    }
    setCurrentStep(1);
    setCurrentView('shop');
  };

  const handleProceedToPayment = () => {
    if (!deliveryAddress.fullName || !deliveryAddress.fullName.trim()) {
      setSnackbar({ open: true, message: 'Please enter your full name!', severity: 'error' });
      return;
    }
    if (!deliveryAddress.phone || !deliveryAddress.phone.trim()) {
      setSnackbar({ open: true, message: 'Please enter your phone number!', severity: 'error' });
      return;
    }
    if (!deliveryAddress.address || !deliveryAddress.address.trim()) {
      setSnackbar({ open: true, message: 'Please enter your address!', severity: 'error' });
      return;
    }
    
    console.log(' Validation passed! Moving to payment step...');
    setCurrentStep(2);
  };

  const handlePlaceOrder = async () => {
    if (!cart || cart.length === 0) {
      setSnackbar({ open: true, message: 'Your cart is empty!', severity: 'error' });
      setCurrentStep(0);
      return;
    }

    setLoading(true);

    try {
      const orderData = {
        items: cart.map(item => ({
          pickleId: item.pickleId,
          quantity: item.quantity,
        })),
        deliveryAddress: deliveryAddress.address,
        deliveryLatitude: deliveryAddress.latitude,
        deliveryLongitude: deliveryAddress.longitude,
        mapLink: deliveryAddress.mapLink || null,
        deliveryCity: deliveryAddress.city || '',
        deliveryPincode: deliveryAddress.pincode || '',
        customerName: deliveryAddress.fullName,
        customerPhone: deliveryAddress.phone,
        notes: `Payment Method: ${paymentMethod}`,
      };

      const result = await dispatch(createOrderRequest(orderData));

      dispatch(clearCartRequest());
      dispatch(clearDeliveryAddress());

      setLastOrderId(result?.payload?.id || Math.floor(Math.random() * 10000));
      setCurrentStep(3);
    } catch (error) {
      setSnackbar({ open: true, message: 'Failed to place order', severity: 'error' });
    } finally {
      setLoading(false);
    }
  };

  const handleBackToShopping = () => {
    setCurrentStep(0);
    setCurrentView('shop');
    dispatch(fetchPicklesRequest());
    dispatch(clearDeliveryAddress());
  };

  const handleNavigateToShop = () => {
    setCurrentStep(0);
    setCurrentView('shop');
  };

  const handleNavigateToAbout = () => {
    setCurrentStep(0);
    setCurrentView('about');
  };

  const handleNavigateToContact = () => {
    setCurrentStep(0);
    setCurrentView('contact');
  };

  const handleNavigateToOrders = () => {
    navigate('/customer/orders');
  };

  const handleNavigateToCart = () => {
    setCartDrawerOpen(true);
  };

  const steps = ['Shop', 'Address', 'Payment', 'Confirmation'];

  if (loading) {
    return (
      <div className="fixed inset-0 bg-white z-[9999] flex flex-col items-center justify-center">
        <div className="w-36 h-36 rounded-full bg-gradient-to-br from-amber-900 to-amber-700 flex items-center justify-center mb-6 animate-pulse shadow-2xl">
          <img src={BrandLogo} alt="Brand Logo" className="w-16 h-16" />
        </div>
        <h1 className="text-3xl font-bold text-amber-900 mb-2">
          Thirushi Pickles
        </h1>
        <p className="text-gray-600">Processing your order...</p>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-100">
      {/* Header */}
      <header className="sticky top-0 bg-white text-blue-600 shadow-sm z-50">
        <div className="flex items-center px-4 py-3 min-h-16">
          {isMobile && (
            <button
              onClick={() => setDrawerOpen(true)}
              className="mr-4 bg-transparent border-none cursor-pointer p-2 flex items-center text-blue-600"
            >
              <MenuIcon />
            </button>
          )}

          <div 
            onClick={handleNavigateToShop}
            className="flex items-center mr-6 cursor-pointer"
          >
            <img
              src={BrandLogo}
              alt="Brand Logo"
              className="w-10 h-10 rounded-full mr-3 shadow-md object-contain"
            />
            <h1 className="text-xl font-bold m-0">
              Thirushi Pickles
            </h1>
          </div>

         
          {currentStep === 0 && !isMobile && (
            <>
            <button
                onClick={handleNavigateToShop}
                className={`bg-transparent border-none cursor-pointer hover:text-red-600 bg-white mr-2 px-4 py-2 text-sm font-medium ${
                  currentView === 'shop' ? 'text-blue-800 font-bold' : 'text-inherit'
                }`}
              >
                Home
              </button>
              <button
                onClick={handleNavigateToAbout}
                className={`bg-transparent border-none hover:text-red-600 cursor-pointer mr-2 px-4 py-2 text-sm font-medium ${
                  currentView === 'about' ? 'text-blue-800 font-bold' : 'text-inherit'
                }`}
              >
                About
              </button>
              <button
                onClick={handleNavigateToContact}
                className={`bg-transparent border-none cursor-pointer hover:text-red-600 mr-4 px-4 py-2 text-sm font-medium ${
                  currentView === 'contact' ? 'text-blue-800 font-bold' : 'text-inherit'
                }`}
              >
                Contact
              </button>

              {currentView === 'shop' && (
                <div className="flex-grow max-w-md mx-auto relative">
                  <div className="relative">
                    <Search className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-600" />
                    <input
                      type="text"
                      placeholder="Search pickles..."
                      value={searchQuery}
                      onChange={(e) => setSearchQuery(e.target.value)}
                      className="w-full py-2 pr-2 pl-10 border border-gray-300 rounded text-sm bg-white"
                    />
                  </div>
                </div>
              )}
            </>
          )}

          <div className="flex-grow" />

          {!isMobile && (
            <>
              <button
                onClick={() => setCartDrawerOpen(true)}
                className="bg-transparent border-none cursor-pointer mr-2 p-2 relative flex items-center text-blue-600"
              >
                <ShoppingCart />
                {totalItems > 0 && (
                  <span className="absolute top-0 right-0 bg-red-500 text-white rounded-full px-1.5 py-0.5 text-xs font-semibold min-w-5 text-center">
                    {totalItems}
                  </span>
                )}
              </button>
              <button
                onClick={() => setUserDrawerOpen(true)}
                className="bg-transparent border-none cursor-pointer mr-2 p-2 flex items-center text-blue-600"
              >
                <Person />
              </button>
              <span className="mr-4 text-sm">
                {user?.username}
              </span>
              <button
                onClick={handleLogout}
                className="bg-blue-600 text-white border-none px-4 py-2 rounded cursor-pointer text-sm font-semibold hover:bg-blue-700"
              >
                LOGOUT
              </button>
            </>
          )}

          {isMobile && (
            <button
              onClick={() => setCartDrawerOpen(true)}
              className="bg-transparent border-none cursor-pointer p-2 relative flex items-center text-blue-600"
            >
              <ShoppingCart />
              {totalItems > 0 && (
                <span className="absolute top-0 right-0 bg-red-500 text-white rounded-full px-1.5 py-0.5 text-xs font-semibold min-w-5 text-center">
                  {totalItems}
                </span>
              )}
            </button>
          )}
        </div>
      </header>

      <NavigationDrawers
        drawerOpen={drawerOpen}
        setDrawerOpen={setDrawerOpen}
        userDrawerOpen={userDrawerOpen}
        setUserDrawerOpen={setUserDrawerOpen}
        cartDrawerOpen={cartDrawerOpen}
        setCartDrawerOpen={setCartDrawerOpen}
        user={user}
        totalItems={totalItems}
        onLogout={handleLogout}
        onNavigateToShop={handleNavigateToShop}
        onNavigateToOrders={handleNavigateToOrders}
        onNavigateToCart={handleNavigateToCart}
        onNavigateToAbout={handleNavigateToAbout}
        onNavigateToContact={handleNavigateToContact}
      />

      <Cart
        cartDrawerOpen={cartDrawerOpen}
        setCartDrawerOpen={setCartDrawerOpen}
        cart={cart}
        cartLoading={cartLoading}
        totalItems={totalItems}
        totalPrice={totalPrice}
        onUpdateQuantity={handleUpdateQuantity}
        onRemoveFromCart={handleRemoveFromCart}
        onCheckoutFromCart={handleCheckoutFromCart}
      />

      {/* Stepper */}
      {currentStep > 0 && currentStep < 3 && currentView === 'shop' && (
        <div className="max-w-7xl mx-auto mt-6 px-4">
          <div className="flex justify-between items-center relative">
            {steps.slice(1).map((label, index) => {
              const stepIndex = index + 1;
              const isActive = stepIndex === currentStep;
              const isCompleted = stepIndex < currentStep;
              
              return (
                <div key={label} className="flex-1 flex flex-col items-center relative">
                  {index > 0 && (
                    <div className={`absolute top-4 left-0 right-1/2 h-0.5 z-0 ${
                      isCompleted || isActive ? 'bg-blue-600' : 'bg-gray-300'
                    }`} style={{ transform: 'translateX(-50%)' }} />
                  )}
                  <div className={`w-8 h-8 rounded-full flex items-center justify-center font-semibold z-10 mb-2 ${
                    isCompleted || isActive ? 'bg-blue-600 text-white' : 'bg-gray-300 text-white'
                  }`}>
                    {isCompleted ? '' : stepIndex}
                  </div>
                  <span className={`text-sm ${
                    isActive || isCompleted ? 'text-blue-600 font-semibold' : 'text-gray-600'
                  }`}>
                    {label}
                  </span>
                </div>
              );
            })}
          </div>
        </div>
      )}

      {/* Main Content */}
      <main className="max-w-7xl mx-auto py-8 px-4">
        {currentView === 'about' && <About />}
        {currentView === 'contact' && <ContactPage />}

        {currentView === 'shop' && (
          <>
            {currentStep === 0 && (
              <PicklesList
                pickles={pickles}
                searchQuery={searchQuery}
                setSearchQuery={setSearchQuery}
                selectedCategory={selectedCategory}
                setSelectedCategory={setSelectedCategory}
                onAddToCart={handleAddToCart}
                onBuyNow={handleBuyNow}
                cartLoading={cartLoading}
                isMobile={isMobile}
              />
            )}

            {currentStep === 1 && (
              <DeliveryAddress
                totalPrice={totalPrice || 0}
                cart={cart || []}
                onProceedToPayment={handleProceedToPayment}
                onBackToShop={handleNavigateToShop}
              />
            )}

            {currentStep === 2 && (
              <PaymentMethod
                paymentMethod={paymentMethod}
                setPaymentMethod={setPaymentMethod}
                cart={cart || []}
                totalPrice={totalPrice || 0}
                onBackToAddress={() => setCurrentStep(1)}
                onPlaceOrder={handlePlaceOrder}
                loading={loading}
              />
            )}

            {currentStep === 3 && (
              <OrderConfirmation
                lastOrderId={lastOrderId}
                onContinueShopping={handleBackToShopping}
              />
            )}
          </>
        )}
      </main>

      {/* Snackbar */}
      {snackbar.open && (
        <div className={`fixed top-5 right-5 text-white px-6 py-4 rounded shadow-lg z-[9999] flex items-center gap-4 min-w-64 animate-slide-in ${
          snackbar.severity === 'success' ? 'bg-green-500' : 
          snackbar.severity === 'error' ? 'bg-red-500' : 'bg-orange-500'
        }`}>
          <span className="flex-1">{snackbar.message}</span>
          <button
            onClick={() => setSnackbar({ ...snackbar, open: false })}
            className="bg-transparent border-none text-white cursor-pointer text-xl p-0 leading-none"
          >
            
          </button>
        </div>
      )}

      <Footer
        onNavigate={navigate}
        onNavigateToShop={handleNavigateToShop}
        onNavigateToAbout={handleNavigateToAbout}
        onNavigateToContact={handleNavigateToContact}
      />

      <style>{`
        @keyframes slide-in {
          from {
            transform: translateX(100%);
            opacity: 0;
          }
          to {
            transform: translateX(0);
            opacity: 1;
          }
        }
        .animate-slide-in {
          animation: slide-in 0.3s ease-out;
        }
      `}</style>
    </div>
  );
};

export default CustomerDashboard;