import React from 'react';
import {
  Box, Typography, Card, IconButton, CircularProgress,
  Button, Drawer, CardMedia
} from '@mui/material';
import { Close, Delete, Add, Remove, ShoppingCart } from '@mui/icons-material';

const Cart = ({
  cartDrawerOpen,
  setCartDrawerOpen,
  cart,
  cartLoading,
  totalItems,
  totalPrice,
  onUpdateQuantity,
  onRemoveFromCart,
  onCheckoutFromCart
}) => {
  const cartDrawer = (
    <Box sx={{ width: { xs: '100vw', sm: 400 }, height: '100%', display: 'flex', flexDirection: 'column' }}>
      <Box sx={{ p: 2, display: 'flex', justifyContent: 'space-between', alignItems: 'center', borderBottom: 1, borderColor: 'divider' }}>
        <Typography variant="h6" sx={{ fontWeight: 700 }}>
          Shopping Cart ({totalItems})
        </Typography>
        <IconButton onClick={() => setCartDrawerOpen(false)}>
          <Close />
        </IconButton>
      </Box>

      <Box sx={{ flexGrow: 1, overflow: 'auto', p: 2 }}>
        {cartLoading ? (
          <Box sx={{ display: 'flex', justifyContent: 'center', py: 8 }}>
            <CircularProgress />
          </Box>
        ) : cart.length === 0 ? (
          <Box sx={{ textAlign: 'center', py: 8 }}>
            <ShoppingCart sx={{ fontSize: 80, color: '#bdbdbd', mb: 2 }} />
            <Typography variant="h6" color="textSecondary">
              Your cart is empty
            </Typography>
            <Typography variant="body2" color="textSecondary" sx={{ mt: 1 }}>
              Add some delicious pickles!
            </Typography>
          </Box>
        ) : (
          <>
            {cart.map((item) => (
              <Card key={item.id} sx={{ mb: 2, p: 2 }}>
                <Box sx={{ display: 'flex', gap: 2, mb: 2 }}>
                  {/* Product Image */}
                  <CardMedia
                    component="img"
                    sx={{ 
                      width: 80, 
                      height: 80, 
                      borderRadius: 1,
                      objectFit: 'cover'
                    }}
                    image={item.pickleImageUrl || item.pickle?.imageUrl || '/placeholder-pickle.jpg'}
                    alt={item.pickleName}
                  />
                  
                  {/* Product Details */}
                  <Box sx={{ flex: 1 }}>
                    <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                      <Box>
                        <Typography variant="subtitle1" sx={{ fontWeight: 600 }}>
                          {item.pickleName}
                        </Typography>
                        <Typography variant="caption" color="textSecondary">
                          Weight: {item.selectedWeight || 250}g
                        </Typography>
                      </Box>
                      <IconButton
                        size="small"
                        onClick={() => onRemoveFromCart(item.id)}
                        sx={{ color: '#d32f2f', height: 'fit-content' }}
                      >
                        <Delete fontSize="small" />
                      </IconButton>
                    </Box>
                    
                    <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                      <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                        <IconButton
                          size="small"
                          onClick={() => onUpdateQuantity(item.id, item.quantity - 1)}
                          sx={{ bgcolor: '#f5f5f5' }}
                        >
                          <Remove fontSize="small" />
                        </IconButton>
                        <Typography sx={{ minWidth: 30, textAlign: 'center', fontWeight: 600 }}>
                          {item.quantity}
                        </Typography>
                        <IconButton
                          size="small"
                          onClick={() => onUpdateQuantity(item.id, item.quantity + 1)}
                          sx={{ bgcolor: '#f5f5f5' }}
                        >
                          <Add fontSize="small" />
                        </IconButton>
                      </Box>
                      <Typography variant="h6" sx={{ fontWeight: 700, color: '#2e7d32' }}>
                        {item.subtotal.toFixed(2)}
                      </Typography>
                    </Box>
                  </Box>
                </Box>
              </Card>
            ))}
          </>
        )}
      </Box>

      {cart.length > 0 && (
        <Box sx={{ p: 2, borderTop: 1, borderColor: 'divider' }}>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 2 }}>
            <Typography variant="h6" sx={{ fontWeight: 700 }}>Total:</Typography>
            <Typography variant="h6" sx={{ fontWeight: 700, color: '#2e7d32' }}>
              {totalPrice.toFixed(2)}
            </Typography>
          </Box>
          <Button
            fullWidth
            variant="contained"
            size="large"
            onClick={() => {
              onCheckoutFromCart();
              setCartDrawerOpen(false);
            }}
            sx={{ bgcolor: '#1976d2', '&:hover': { bgcolor: '#1565c0' } }}
          >
            Proceed to Checkout
          </Button>
        </Box>
      )}
    </Box>
  );

  return (
    <Drawer anchor="right" open={cartDrawerOpen} onClose={() => setCartDrawerOpen(false)}>
      {cartDrawer}
    </Drawer>
  );
};

export default Cart;