import React from 'react';
import {
  Box, Drawer, List, ListItem, ListItemIcon, ListItemText, Divider,
  Typography, Badge
} from '@mui/material';
import {
  Home, Receipt, ShoppingCart, Logout, Info, Phone, Person
} from '@mui/icons-material';
import BrandLogo from '../assets/BrandLogo.png'

const NavigationDrawers = ({
  drawerOpen,
  setDrawerOpen,
  userDrawerOpen,
  setUserDrawerOpen,
  user,
  totalItems,
  onLogout,
  onNavigateToShop,
  onNavigateToOrders,
  onNavigateToCart,
  onNavigateToAbout,
  onNavigateToContact
}) => {
  const mobileDrawer = (
    <Box sx={{ width: 280, pt: 2 }}>
      <Box sx={{ px: 3, mb: 3 }}>
        <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
          <Box sx={{
            width: 50,
            height: 50,
            borderRadius: '50%',
            background: 'linear-gradient(135deg, #8B4513 0%, #D2691E 100%)',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            mr: 2,
            boxShadow: '0 4px 12px rgba(139, 69, 19, 0.3)'
          }}>
            <Typography sx={{ fontSize: '24px' }}><img
    src={BrandLogo}
    alt="Brand Logo"
    style={{ width: '64px', height: '64px' }}
  /></Typography>
          </Box>
          <Box>
            <Typography variant="h6" sx={{ fontWeight: 700, color: '#1976d2' }}>
              Thirushi Pickles
            </Typography>
            <Typography variant="caption" color="textSecondary">
              Welcome, {user?.username}!
            </Typography>
          </Box>
        </Box>
      </Box>
      <Divider />
      <List>
        <ListItem button onClick={() => { onNavigateToShop(); setDrawerOpen(false); }}>
          <ListItemIcon><Home sx={{ color: '#1976d2' }} /></ListItemIcon>
          <ListItemText primary="Shop" />
        </ListItem>
        <ListItem button onClick={() => { onNavigateToCart(); setDrawerOpen(false); }}>
          <ListItemIcon>
            <Badge badgeContent={totalItems} color="error">
              <ShoppingCart sx={{ color: '#1976d2' }} />
            </Badge>
          </ListItemIcon>
          <ListItemText primary="Cart" />
        </ListItem>
        <ListItem button onClick={() => { onNavigateToOrders(); setDrawerOpen(false); }}>
          <ListItemIcon><Receipt sx={{ color: '#1976d2' }} /></ListItemIcon>
          <ListItemText primary="My Orders" />
        </ListItem>
        <ListItem button onClick={() => { onNavigateToAbout(); setDrawerOpen(false); }}>
          <ListItemIcon><Info sx={{ color: '#1976d2' }} /></ListItemIcon>
          <ListItemText primary="About Us" />
        </ListItem>
        <ListItem button onClick={() => { onNavigateToContact(); setDrawerOpen(false); }}>
          <ListItemIcon><Phone sx={{ color: '#1976d2' }} /></ListItemIcon>
          <ListItemText primary="Contact Us" />
        </ListItem>
      </List>
      <Divider />
      <List>
        <ListItem button onClick={onLogout}>
          <ListItemIcon><Logout sx={{ color: '#d32f2f' }} /></ListItemIcon>
          <ListItemText primary="Logout" />
        </ListItem>
      </List>
    </Box>
  );

  const userDrawer = (
    <Box sx={{ width: 280, pt: 2 }}>
      <Box sx={{ px: 3, mb: 3 }}>
        <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
          <Box sx={{
            width: 60,
            height: 60,
            borderRadius: '50%',
            bgcolor: '#1976d2',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            mr: 2
          }}>
            <Person sx={{ fontSize: 35, color: 'white' }} />
          </Box>
          <Box>
            <Typography variant="h6" sx={{ fontWeight: 700 }}>
              {user?.username}
            </Typography>
            <Typography variant="body2" color="textSecondary">
              {user?.email || 'Customer'}
            </Typography>
          </Box>
        </Box>
      </Box>
      <Divider />
      <List>
        <ListItem button onClick={() => { onNavigateToShop(); setUserDrawerOpen(false); }}>
          <ListItemIcon><Home sx={{ color: '#1976d2' }} /></ListItemIcon>
          <ListItemText primary="Shop" />
        </ListItem>
        <ListItem button onClick={() => { onNavigateToOrders(); setUserDrawerOpen(false); }}>
          <ListItemIcon><Receipt sx={{ color: '#1976d2' }} /></ListItemIcon>
          <ListItemText primary="My Orders" />
        </ListItem>
        <ListItem button onClick={() => { onNavigateToCart(); setUserDrawerOpen(false); }}>
          <ListItemIcon>
            <Badge badgeContent={totalItems} color="error">
              <ShoppingCart sx={{ color: '#1976d2' }} />
            </Badge>
          </ListItemIcon>
          <ListItemText primary="Cart" />
        </ListItem>
        <ListItem button onClick={() => { onNavigateToAbout(); setUserDrawerOpen(false); }}>
          <ListItemIcon><Info sx={{ color: '#1976d2' }} /></ListItemIcon>
          <ListItemText primary="About Us" />
        </ListItem>
        <ListItem button onClick={() => { onNavigateToContact(); setUserDrawerOpen(false); }}>
          <ListItemIcon><Phone sx={{ color: '#1976d2' }} /></ListItemIcon>
          <ListItemText primary="Contact Us" />
        </ListItem>
      </List>
      <Divider />
      <List>
        <ListItem button onClick={onLogout}>
          <ListItemIcon><Logout sx={{ color: '#d32f2f' }} /></ListItemIcon>
          <ListItemText primary="Logout" />
        </ListItem>
      </List>
    </Box>
  );

  return (
    <>
      <Drawer anchor="left" open={drawerOpen} onClose={() => setDrawerOpen(false)}>
        {mobileDrawer}
      </Drawer>

      <Drawer anchor="right" open={userDrawerOpen} onClose={() => setUserDrawerOpen(false)}>
        {userDrawer}
      </Drawer>
    </>
  );
};

export default NavigationDrawers;